export default{
	data(){
		return {
			InputBottom:0
		}
	},
	methods:{
		 InputFocus(e) {
		    this.setData({
		      InputBottom: e.detail.height
		    })
		  },
		  InputBlur(e) {
		      this.setData({
		        InputBottom: 0
		      })
		    }
	}
	
}
